<template>
<div>
    <Header></Header>
  <Sidebar></Sidebar>

  <div class="content-wrapper">
  <!-- Content Header (Page header) -->
    <div class="content-header">
      <div class="container-fluid">
        <div class="row mb-2">
          <div class="col-sm-6">
           <!-- <h1 class="m-0 text-dark">Add Courier Name</h1>-->
          </div><!-- /.col -->
          <div class="col-sm-6">
            <ol class="breadcrumb float-sm-right">
              <li class="breadcrumb-item"><a href="/courier/CourierConfig">Home</a></li>
              <li class="breadcrumb-item active">Courier Configuration</li>
            </ol>
          </div><!-- /.col -->

        </div><!-- /.row -->
      </div><!-- /.container-fluid -->
    </div>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row" style="margin-left: 10px;margin-right: 10px">
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Student Courier Rate</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <!--<div class="form-group">-->
                    <div class="row">
                      <div class="col-md-4">
                    <label for="exampleInputEmail1">Minimum Price<span><h6>(for 1.2KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="sminRate" v-autofocus></span></div>
                 <div class="col-md-4">
                    <label for="exampleInputEmail1">Donkey Price<span><h6>(for 1.2KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="dminRate"></span></div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Total Price<span><h6>(for 1.2KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="tminRate" disabled></span></div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Additional Price<span><h6>(for Every 1KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="saddRate" v-autofocus></span></div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Donkey Price<span><h6>(for Every 1KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="daddRate" v-autofocus></span></div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Total Price<span><h6>(for Every 1KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="taddRate" disabled v-autofocus></span></div>
                  </div></div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div>
 
</div>
 </div>
</div>
</template>

<script>
import Sidebar from '../../components/sidebar'
import Header from '../../components/Header'
export default {
  name: 'CourierConfig',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
                }
    }
  },
  data () {
 return {item: {}, sminRate: '', saddRate: '',dminRate:'',daddRate:''
  }
},
computed: {
    tminRate:function(){
 return parseInt(this.sminRate) + parseInt(this.dminRate)
    },
    taddRate:function(){
return parseInt(this.saddRate)+parseInt(this.daddRate)
    }
},
  created: function () { this.autoInc()},
  methods: {
    autoInc () {
      
        let uri = this.$hostName + '/courier/CourierConfig'
                this.axios.get(uri).then((response) => {
                    this.sminRate= response.data[0]['studentMinRate'],
                    this.saddRate= response.data[0]['studentAddlRate'],
                    this.dminRate= response.data[0]['DMinRate'],
                    this.daddRate= response.data[0]['DAddlRate']
                })            
    },
    addItem () {
      // let uri = this.$hostName + '/courier/CourierCompany/add'
      //  if (this.$route.params.id != undefined)
        let uri = this.$hostName+ '/courier/CourierConfig/update';
        this.item.sminRate= this.sminRate
        this.item.saddRate= this.saddRate
          this.item.dminRate= this.dminRate
        this.item.daddRate= this.daddRate
         this.axios.post(uri, this.item).then((response) =>
      { this.$router.push({name: 'Dashboard'}) })
    }

  },
  components: {
    Sidebar,
    // App,
    Header
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



