<template>
  <div>
    <Header></Header>
    <Sidebar></Sidebar>

    <div class="content-wrapper">
      <!-- Content Header (Page header) -->
      <div class="content-header">
        <div class="container-fluid">
          <div class="row mb-2">
            <div class="col-sm-6">
              <h1 class="m-0 text-dark">Daily Discount</h1>
            </div>
            <!-- /.col -->
            <div class="col-sm-6">
              <ol class="breadcrumb float-sm-right">
                <li class="breadcrumb-item">
                  <a href="#">Home</a>
                </li>
                <li class="breadcrumb-item active">Daily Discount</li>
              </ol>
            </div>
            <!-- /.col -->
          </div>
          <!-- /.row -->
        </div>
        <!-- /.container-fluid -->
      </div>
      <!-- /.content-header -->

      <!-- Content Body-->

      <div class="col-md-12">
        <div class="card">
          <div class="card-header">
            <h3 class="card-title text-right">
              <router-link :to="{ name: 'DailyDiscountadd' }" class="btn btn-primary">Add Daily Discount</router-link>
            </h3>
          </div>
          <!-- /.card-header -->
          <div class="card-body p-0">
            <table class="table table-condensed">
              <tr>
                <th style="width: 5%">#</th>
                <th style="width: 25%">Day</th>
                <th style="width: 25%">ServiceArea</th>
                <th style="width: 25%;">ServiceType</th>
                <th style="width: 15%;">Discount</th>
                <th style="width: 5%;">Action</th>
              </tr>
              <tbody>
                <tr v-for="(item,index) in items">
                  <td>{{ item.id }}</td>
                  <td>{{ item.Day }}</td>
                  <td>{{ item.ServiceArea }}</td>
                  <td>{{ item.ServiceType }}</td>
                  <td>{{ item.Discount }}</td>
                  <td>
                    <router-link :to="{name: 'DailyDiscountadd',params:{id: item.id}}" class="btn btn-primary">Edit</router-link>
                  </td>
                  <td>
                    <button class="btn btn-danger" v-on:click="deleteItem(item.id,index)">Delete</button>
                  </td>
                </tr>
              </tbody>

            </table>
          </div>
          <!-- /.card-body -->
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import Sidebar from '../../components/sidebar'
  import Header from '../../components/Header'
  export default {
  /* eslint-disable */
  name: 'ProductTypevw',
  data(){return{items:[]}},
  created:function(){
  this.FetchItem();
  },
  methods:{ FetchItem(){
  let uri = this.$hostName+ '/common/DailyDiscount';
  this.axios.get(uri).then((response) => {
  this.items = response.data;
  });
  },
  deleteItem(id1,ri){
  var r=  confirm('Are u want to Delete?');
  if(r==true){
  let uri = this.$hostName+ '/common/DailyDiscount/Del/'+id1;
  this.items.splice(ri, 1);
  this.axios.get(uri).then((response) => {
  //console.log(response.data);
  this.$router.push({name: 'DailyDiscountvw'})
  });}
  }
  },
  components: {
  Sidebar,
  //App,
  Header
  }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="">

</style>



