<template>
<div>
    <Header></Header>
  <Sidebar></Sidebar>

  <div class="content-wrapper">
  <!-- Content Header (Page header) -->
    <div class="content-header">
      <div class="container-fluid">
        <div class="row mb-2">
          <div class="col-sm-6">
           <!-- <h1 class="m-0 text-dark">Add Courier Name</h1>-->
          </div><!-- /.col -->
          <div class="col-sm-6">
            <ol class="breadcrumb float-sm-right">
              <li class="breadcrumb-item"><a href="/common/ServiceArea">Home</a></li>
              <li class="breadcrumb-item active">Service Area</li>
            </ol>
          </div><!-- /.col -->


        </div><!-- /.row -->
      </div><!-- /.container-fluid -->
    </div>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Service Area</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service Area</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Service Area" v-model="servicearea" v-autofocus required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Latitude</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Latitude" v-model="lati" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Longitude</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Longitude" v-model="long" required>
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
import Sidebar from '../../components/sidebar'
import Header from '../../components/Header'
export default {
  name: 'ServiceAreaadd',
  directives: {
            'autofocus': {
                inserted(el) {
                    el.focus();
                }
            }
        },
  data(){return{item:{},id:'',servicearea:'',long:'',lati:''
}},
  created:function(){ this.autoInc();},
  methods:{
    autoInc(){
      if (this.$route.params.id==undefined) {
           let uri = this.$hostName+ '/common/ServiceArea/Auto';
              this.axios.get(uri).then((response) => {
                  this.id = response.data[0]["id"];
                  this.item.id =this.id;
              });
            }
            else{
                let uri = this.$hostName+ '/common/ServiceArea/edit/' + this.$route.params.id;
                this.axios.get(uri).then((response) => {
                    //this.item = response.data;
                    this.id= response.data[0]["id"];
                    this.item.id=this.id;
                    this.servicearea= response.data[0]["ServiceArea"];
                    this.long= response.data[0]["Longitude"];
                    this.lati= response.data[0]["Latitude"];
                    });
            }
            this.$refs.search.focus();
    },
         addItem(){
        let uri = this.$hostName+ '/common/ServiceArea/add';
        if(this.$route.params.id!=undefined)         
         uri = this.$hostName+ '/common/ServiceArea/update';
        this.item.id=this.id;
        this.item.servicearea=this.servicearea;
        this.item.long=this.long;
        this.item.lati=this.lati;
         this.axios.post(uri, this.item).then((response) => 
         { this.$router.push({name: 'ServiceAreavw'})})
}

  },
  components: {
    Sidebar,
    //App,
    Header
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



