<template>
<div>
    <Header></Header>
  <Sidebar></Sidebar>

  <div class="content-wrapper">
  <!-- Content Header (Page header) -->
    <div class="content-header">
      <div class="container-fluid">
        <div class="row mb-2">
          <div class="col-sm-6">
           <!-- <h1 class="m-0 text-dark">Add Courier Name</h1>-->
          </div><!-- /.col -->
          <div class="col-sm-6">
            <ol class="breadcrumb float-sm-right">
              <li class="breadcrumb-item"><a href="/courier/CourierRate">Home</a></li>
              <li class="breadcrumb-item active">Add Courier Rate</li>
            </ol>
          </div><!-- /.col -->


        </div><!-- /.row -->
      </div><!-- /.container-fluid -->
    </div>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Courier Rate</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Courier Name</label>                    
                    <select class="form-control select2" style="width: 100%;" v-model="couriername" value="Select Courier" required v-autofocus>
                      <option value="">Select Courier</option>
                      <option v-for="dept in listCourier" v-bind:value="dept.CourierName">
                      {{dept.CourierName}}
                      </option>
                    </select>                    
                  </div>
                    <div class="form-group">
                    <label for="exampleInputPassword1">Courier Type</label>
                    <select class="form-control select2" style="width: 100%;" @change="onChange" v-model="selectedType" required>
                    <option selected="selected" value="Document">Document</option>
                    <option value="Box">Box</option>
                    </select>                    
                  </div>
                  <div class="form-group" v-if="document1">
                    <div class="row">
                    <div class="col-md-6"><label for="exampleInputPassword1">Length (inch)</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Length" v-model="len">
                  </div>
                  <div class="col-md-6"><label for="exampleInputPassword1">Breadth (inch)</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Breadth" v-model="wid" >
                  </div>
                </div>
                  </div>
                  <div class="form-group" v-if="box1">
                    <label for="exampleInputPassword1">Item Weight (kg)</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Per KG" v-model="perkg" >
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">From State</label>                    
                    <select class="form-control select2" style="width: 100%;" v-model="fromState" required>
                      <option value="">Select State</option>
                      <option  v-for="dept in listState" v-bind:value="dept.State">
                      {{dept.State}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">To State</label>                    
                    <select class="form-control select2" style="width: 100%;" v-model="ToState" required>
                      <option value="">Select State</option>
                      <option v-for="dept in listState" v-bind:value="dept.State">
                      {{dept.State}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Select Area</label>
                    <select class="form-control select2" style="width: 100%;" v-model="area">
                    <option selected="selected" value="Local">Local</option>
                    <option value="By City">By City</option>
                    <option value="By State">By State</option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Courier Price</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Courier Price" v-model="courierprice" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Donkey Price</label>                    
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Donkey Price" v-model="donkeyprice" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Offer Price</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Offer Price" v-model="offerprice" required>
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
import Sidebar from '../../components/sidebar'
import Header from '../../components/Header'
export default {
  name: 'CourierRateadd',
  directives: {
            'autofocus': {
                inserted(el) {
                    el.focus();
                }
            }
        },
  data() { return{item:{},id:'',couriername:'',selectedType:'Document',len:'',wid:'',perkg:'',fromState:'',ToState:'',area:'Local',courierprice:'',donkeyprice:'',offerprice:'',listCourier:[],listState:[],document1:true,box1:false
}},

  created:function() { this.autoInc() },
  methods: {
     onChange:function(){
      if(this.selectedType=="Document")        
        { this.document1=true;this.box1=false;}
      else if(this.selectedType=="Box")
        {this.document1=false;this.box1=true;}
      return;
        },
    autoInc(){
      let uris = this.$hostName + '/common/StateMaster';
              this.axios.get(uris).then((response) => {
                  this.listState = response.data;
              });            

      let uric = this.$hostName+ '/courier/CourierCompany';
              this.axios.get(uric).then((response) => {
                  this.listCourier = response.data;
              });

      if (this.$route.params.id==undefined) {
           let uri = this.$hostName+ '/courier/CourierRate/Auto';
              this.axios.get(uri).then((response) => {
                  this.id = response.data[0]["id"];
                  this.item.id =this.id;
              });
            }
            else{
                let uri = this.$hostName+ '/courier/CourierRate/edit/' + this.$route.params.id;
                this.axios.get(uri).then((response) => {
                    //this.item = response.data;
                    this.id= response.data[0]["id"];
                    this.couriername= response.data[0]["CourierName"];
                    this.selectedType= response.data[0]["CourierType"];
                    this.len= response.data[0]["DocumentLength"];
                    this.wid= response.data[0]["DocumentBreadth"];
                    this.perkg= response.data[0]["BoxWeight"];
                    this.fromState= response.data[0]["FromState"];
                    this.ToState= response.data[0]["ToState"];
                    this.area= response.data[0]["Area"];
                    this.courierprice= response.data[0]["CPrice"];
                    this.donkeyprice= response.data[0]["DPrice"];
                    this.offerprice= response.data[0]["OPrice"];
                    this.onChange();
                });
            }
    },
         addItem(){
        let uri = this.$hostName+ '/courier/CourierRate/add';
        if(this.$route.params.id!=undefined)         
         uri = this.$hostName+ '/courier/CourierRate/update';
        this.item.id=this.id;
        this.item.couriername=this.couriername;
        this.item.selectedType=this.selectedType;
        this.item.len=this.len;
        this.item.wid=this.wid;
        this.item.perkg=this.perkg;
        this.item.fromState=this.fromState;
        this.item.ToState=this.ToState;
        this.item.area=this.area;
        this.item.courierprice=this.courierprice;
        this.item.donkeyprice=this.donkeyprice;
        this.item.offerprice=this.offerprice;
        this.axios.post(uri, this.item).then((response) => 
         { this.$router.push({name: 'CourierRatevw'})})
}

  },
  components: {
    Sidebar,
    //App,
    Header
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



