<template>
<div>
    <Header></Header>
  <Sidebar></Sidebar>

  <div class="content-wrapper">
  <!-- Content Header (Page header) -->
    <div class="content-header">
      <div class="container-fluid">
        <div class="row mb-2">
          <div class="col-sm-6">
           <!-- <h1 class="m-0 text-dark">Add Courier Name</h1>-->
          </div><!-- /.col -->
          <div class="col-sm-6">
            <ol class="breadcrumb float-sm-right">
              <li class="breadcrumb-item"><a href="/common/DailyDiscount">Home</a></li>
              <li class="breadcrumb-item active">Add DailyDiscount</li>
            </ol>
          </div><!-- /.col -->


        </div><!-- /.row -->
      </div><!-- /.container-fluid -->
    </div>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add DailyDiscount</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Day</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedday" required v-autofocus>
                    <option value="">Select Day</option>
                    <option v-for="dept in days" v-bind:value="dept.days">
                      {{dept.Day}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service Area</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedSarea" required>
                    <option value="">Select ServiceArea</option>
                    <option v-for="dept in listServicearea" v-bind:value="dept.ServiceArea">
                      {{dept.ServiceArea}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service Type</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedType" required>
                    <option value="">Select ServiceType</option>
                    <option v-for="dept in listType" v-bind:value="dept.Type">
                      {{dept.Type}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Discount Price</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Discount Price" v-model="cname" required>
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
import Sidebar from '../../components/sidebar'
import Header from '../../components/Header'
export default {
  name: 'DailyDiscountadd',
  directives: {
            'autofocus': {
                inserted(el) {
                    el.focus();
                }
            }
        },
  data(){return{item:{},id:'',cname:'',desc:'' ,days:[
      { id:1,Day:'Monday' },
      { id:2,Day:'Tuesday' },
      { id:3,Day:'Wednesday' },
      { id:4,Day:'Thursday' },
      { id:5,Day:'Friday' },
      { id:6,Day:'Saturday' },
      { id:7,Day:'Sunday' }
      ],listServicearea:[],selectedday:'',selectedSarea:'',selectedType:'',listType:[
      {Type:'Courier'},
      {Type:'Parcel'},
      {Type:'Sharing'},
      {Type:'Rental'}
      ]
}},
  created:function(){ this.autoInc();},
  methods:{
    autoInc(){
      let uris = this.$hostName + '/common/ServiceArea';
              this.axios.get(uris).then((response) => {
                  this.listServicearea = response.data;
              });            
     
      if (this.$route.params.id==undefined) {
           let uri = this.$hostName+ '/common/DailyDiscount/Auto';
              this.axios.get(uri).then((response) => {
                  this.id = response.data[0]["id"];
                  this.item.id =this.id;
              });
            }
            else{
                let uri = this.$hostName+ '/common/DailyDiscount/edit/' + this.$route.params.id;
                this.axios.get(uri).then((response) => {
                    //this.item = response.data;
                    this.id= response.data[0]["id"];
                    this.item.id=this.id;
                    this.cname= response.data[0]["State"];
                    this.desc= response.data[0]["Description"];
                    });
            }
            this.$refs.search.focus();
    },
         addItem(){
        let uri = this.$hostName+ '/common/DailyDiscount/add';
        if(this.$route.params.id!=undefined)         
         uri = this.$hostName+ '/common/DailyDiscount/update';
        this.item.id=this.id;
        this.item.cname=this.cname;
        this.item.desc=this.desc;
         this.axios.post(uri, this.item).then((response) => 
         { this.$router.push({name: 'DailyDiscountvw'})})
}

  },
  components: {
    Sidebar,
    //App,
    Header
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



